import axios, { Axios, CreateAxiosDefaults } from 'axios'
import { IntegrationClientModel } from '../integration-client.model'

export class EulerClient implements IntegrationClientModel {
  // Axios instance
  private axios: Axios

  constructor() {
    this.axios = this.initAxios()
  }

  /**
   * Axios initialization
   */
  private initAxios() {
    const options: CreateAxiosDefaults = {
      baseURL: 'https://app.euler.finance/api/v1/',
      headers: {
        'content-type': 'application/json',
      },
    }
    return axios.create(options)
  }

  // Methods

  public async getAccountVaults(account: string): Promise<string[]> {
    return this.axios
      .request({
        url: `accounts/vaults?chainId=1&account=${account}`,
        method: 'GET',
        responseType: 'json',
      })
      .then((response) => {
        if (!response?.data) {
          return []
        }

        return response.data.map(
          (d: { account: string; vault: string }) => d.vault
        )
      })
      .catch(() => [])
  }
}
